<template>
  <v-app id="keep">
    <v-app-bar app clipped-left>
      <v-app-bar-nav-icon @click="drawer = !drawer" />
      <v-toolbar-title class="secondary--text">
        Tarkov Resource Planner v{{version}}
        <span class="tiny">for Tarkov v0.12.3</span>
      </v-toolbar-title>
      <v-spacer />
      <v-btn :color="this.$store.state.dirty?'primary':''" @click="save">Save to cookie!</v-btn>
      <v-snackbar v-model="snackbar" :timeout="1000">
        Saved to cookie!
        <v-btn color="pink" text @click="snackbar = false">Close</v-btn>
      </v-snackbar>
    </v-app-bar>

    <v-navigation-drawer v-model="drawer" app clipped>
      <div class="flex-col h100">
        <div class="flex-grow">
          <v-list-item to="/info">
            <v-list-item-action>
              <v-icon>info</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Info</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-divider></v-divider>
          <v-list-item to="/character">
            <v-list-item-action>
              <v-icon>account_circle</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Character</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/quests">
            <v-list-item-action>
              <v-icon>playlist_add_check</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Quests</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/hideout">
            <v-list-item-action>
              <v-icon>playlist_add_check</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Hideout</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/checklist">
            <v-list-item-action>
              <v-icon>check_box</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Checklist</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-divider></v-divider>
          <v-list-item to="/port">
            <v-list-item-action>
              <v-icon>vertical_align_center</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Import/Export</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/compare">
            <v-list-item-action>
              <v-icon>group</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>Quest comparator</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
        <div class="flex-grow h100"></div>
        <div class="flex-shrink">
          <v-list-item>
            <v-list-item-action></v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>by Astarch</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
      </div>
    </v-navigation-drawer>

    <v-content>
      <v-container align-start fluid>
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </v-container>
    </v-content>
  </v-app>
</template>
<script>
import { version } from "../package.json";

export default {
  name: "App",
  components: {},
  data() {
    return {
      version,
      drawer: true,
      snackbar: false,
      tab: null,
      items: ["Quests", "Hideout", "Stocked", "Info"],
      text:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat."
    };
  },
  methods: {
    save() {
      this.$cookies.set("state", this.$store.state);
      this.$store.commit("setDirty", false);
      console.log();
      this.snackbar = true;
    }
  },
  created() {
    var cookieValue = this.$cookies.get("state");
    if (cookieValue) {
      this.$store.replaceState(cookieValue);
    }
  }
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  /* color: #e3e3e3; */
  /* margin-top: 60px; */
}

/*
  background light 171715
  background dark 0e0e0e
  text light aeaeb0
  button background 9a8866
*/
.v-input--checkbox {
  margin: 0px;
  padding: 0px;
  height: 20px;
}
.v-treeview-node__root {
  min-height: 0px !important;
}

html,
body {
  color: #9a8866;
}
.tiny {
  font-size: 10px;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.flex {
  flex-grow: 1;
  flex-grow: 0;
}

.flex-grow {
  flex-grow: 1;
}

.flex-shrink {
  flex-grow: 1;
}
.h100 {
  height: 100%;
}
</style>
